.forecast-info {
    grid-area: d;
    padding: 20px;
}

.forecast-info table{
    margin: auto;
    width: 90%;
    border-collapse: collapse;
    table-layout: auto;
}
.forecast-info td, .forecast-info th {
    border: 1px solid #000000;
}

.forecast {
    margin: 20px;
}


.forecast-button {
    grid-area: c;
    text-align: center;
    margin-left: 20px;
}

.forecast-info .saveForecastInfoButton, .forecast-info .save-info {
    margin: 10px;
    display: flex;
    justify-content: center;
}
.saveForecastInfoButton button {
    padding: 7px;
    cursor: pointer;
    background: #3861FB;
    color: white;
    border: 0px;
    border-radius: 8px;
    font-weight: 600;
    width: auto;
    height: 32px;
    font-size: 12px;
    padding: 0px 16px;
    line-height: 18px;
}
#forecastBtn{
    cursor: pointer;
    background: #3861FB;
    color: white;
    border: 0px;
    border-radius: 8px;
    font-weight: 600;
    width: auto;
    height: 32px;
    font-size: 12px;
    padding: 0px 16px;
    line-height: 18px;
}
#forecastBtn:hover{
    background-color: #5d7df1;
}


.graph-info {
    box-shadow: rgb(88 102 126 / 8%) 0px 4px 24px, rgb(88 102 126 / 12%) 0px 1px 2px;
    background-color: #FFFFFF;
    border-radius: 8px;
    margin: 35px auto 35px auto;
    height: 350px;
    width: 810px;
    text-align: center;
}

.graph-info img{
    width: 50%;
    height: 350px;
    float: right;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;

}


.graph-info .expl-text{
    width: 50%;
    float: left;
    text-align: center;

}

.graph-info .expl-text p{
    padding: 25px;
}

.graph-info .expl-text h3{
    padding-top: 15px;
}

.recharts-layer.recharts-cartesian-axis.recharts-xAxis.xAxis {
    font-size: small;
  }

#forecastBtn {
    font-family: 'Barlow Condensed', sans-serif;
    font-size: medium;
}

